.about-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

.about-desc-container {
  margin: 2em auto;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

div.about-desc-container img {
  margin-right: 2em;
}

div.what-i-do-container div h2 {
  font-family: "Archivo Black";
  color: #222222;
}

div.about-desc-container p {
  margin: 0;
}

.about-picture {
  width: 40vh;
  height: 40vh;
  border-radius: 10px;
}

.about-desc-text {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

div.about-desc-text p {
  margin-bottom: 1em;
}

div h1 {
  margin-top: 1em;
  margin-bottom: 0.4em !important;
  font-family: "Archivo Black";
  color: #222222;
}

div hr {
  width: 40%;
  margin-top: 0;
  margin-bottom: 0.5em;
  border: 2px solid rgb(92, 176, 233);
  background-color: rgb(92, 176, 233);
  border-radius: 20px;
}

.what-i-do-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

#what-i-do {
  font-family: "Tenor Sans";
}

div.what-i-do-container div i {
  color: rgb(92, 176, 233);
}

div.what-i-do-container div p {
  margin: 0em;
}

div.what-i-do-container div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

div.what-i-do-container div {
  margin: 1em 4em;
}

@media(max-width: 980px) {
  .about-desc-container {
    -ms-flex-pack: center;
        justify-content: center;
    margin: 1em auto;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }
  div.about-desc-container img {
    margin-right: 0em;
  }
  div.about-desc-container p {
    margin-top: 1em;
  }
  div.about-desc-text p:first-of-type {
    margin-top: 2em;
    margin-bottom: 0em;
  }
  div.about-desc-text p:nth-child(3) {
    margin-top: 0em;
    margin-bottom: 1em;
  }
  div h1 {
    margin-top: 1em;
    margin-bottom: 1em;
  }
  .what-i-do-container{
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-direction: column;
        flex-direction: column;
  }
  div.what-i-do-container div {
    margin: 1em 1em;
  }
}


