.projects-container {
  width: 85%;
  margin: 2em auto;
  font-size: 20px;
  line-height: 30px;
  text-align: justify;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-direction: column;
      flex-direction: column;
}

div.projects-container div h1 {
  text-align: center;
  margin-top: 2em;
  margin-bottom: 2em;
}

div.projects-container div p {
  text-align: center;
  margin-top: 0em;
  margin-bottom: 3em;
}

.projects-shots {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: center;
      justify-content: center;
  margin: 1em auto;
  margin-top: 2em !important;
  text-align: center;
}

.projects-shots-pair {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.a-project {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}

div.projects-shots-pair div:first-of-type {
  margin-right: 3em;
}

.project-picture {
  width: 80vh;
  height: 65vh;
}

div.a-project h2 {
  font-weight: 100;
  color: rgb(92, 176, 233);
}

.projects-button {
  border: none;
  border-radius: 30px;
  padding: 1em 2.5em;
  background-color: #222222;
  font-weight: bold;
  font-family: "Roboto";
  margin-bottom: 1.5em;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
  color: #fff;
}

.projects-button:hover {
  cursor: pointer;
  background-color: rgb(92, 176, 233);
  color: #000;
}

@media(max-width: 980px) {
  .project-picture {
    width: 50vh;
    height: 40vh;
  }
  .projects-shots {
    margin: 1em 1em;
  }
}

@media(max-width: 1500px) {
  .projects-shots-pair {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
  }
  div.projects-shots-pair div:first-of-type {
    margin-right: 0em;
  }
}


